<template>
  <section class="hero">
    <div class="container1">
      <div class="left-side">
        <div class="image-container">
          <img src="@/assets/images/profile.jpg" alt="Hazem Mostafa" class="profile-image" />
        </div>
      </div>
      <div class="right-side">
        <div class="text-content">
          <h1>{{ text1 }}</h1><br>
          <h2>{{ text2 }}</h2>
          <p>{{ skillsText }}</p>
          <div class="social-icons">
          <a href="https://www.linkedin.com/in/hmhazem99">
            <img class="gif" src="@/assets/icons/linkedin.gif" alt="Location" style="width: 60px; height: 60px; vertical-align: middle;" />
          </a>
          <a href="https://www.facebook.com/mmhazem99/">
            <img class="gif" src="@/assets/icons/facebook.gif" alt="Location" style="width: 60px; height: 60px; vertical-align: middle;" />
          </a>
          <a href="https://github.com/haziim99">
            <img class="gif" src="@/assets/icons/git.gif" alt="Location" style="width: 60px; height: 60px; vertical-align: middle;" />
          </a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>



<script>
export default {
  data() {
    return {
      text1: '',
      text2: '',
      skillsText: '',
    };
  },
  mounted() {
    const text1 = '<Hi.. I\'m Hazem Mostafa />';
    const text2 = 'A Full Stack Developer.. Bringing Ideas to Life';
    const skillsText = 'ANGULAR | JAVA | MySQL';

    this.typeWriter('text1', text1, 150);
    setTimeout(() => this.typeWriter('text2', text2, 150), text1.length * 150 + 500);
    setTimeout(() => this.typeWriter('skillsText', skillsText, 150), (text1.length + text2.length) * 150 + 1000);
  },
  methods: {
    typeWriter(dataProperty, text, speed) {
      let i = 0;
      const interval = setInterval(() => {
        this[dataProperty] += text.charAt(i);
        i++;
        if (i === text.length) {
          clearInterval(interval);
        }
      }, speed);
    }
  }
};
</script>

<style>
* { 
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Marcellus', serif;
  
}



.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: 'Montserrat', sans-serif;
}

.container1 {
  background-color: #fff;
  border-radius: 40px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  display: flex;
  min-width: 1070px;
  height: 570px;
  padding: 30px;
  color: #fff;
}

.right-side {
  flex: 2;
  min-width: 660px;
  background-color: #512da8;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 150px 20px 20px 100px;
}

.left-side {
  flex: 1;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 40px;
}

.image-container {
  position: relative;
  width: 300px;
  height: 300px;
  border-radius: 20px;
  overflow: hidden;
  background: rgba(72, 0, 241, 0.3);
  animation: waveAnimation 5s ease-in-out infinite;
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.text-content {
  margin-left: 30px;
  width: 600px;
}

h1, h2, p {
  line-height: 2; /* إضافة مسافة بين السطور */
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

p {
  font-size: 1.25rem;
  margin-bottom: 20px;
}

.emoji {
  font-size: 3rem;
  margin-right: 10px; /* إضافة مسافة بعد الإيموجي */
  animation: emojiAnimation 1s ease-in-out infinite;
}

.name {
  margin-left: 10px;
}

h2.subheading {
  font-size: 1.5rem;
  margin-bottom: 20px;
  font-weight: 400;
  animation: textAnimation 3s ease-in-out;
}

p.skills {
  font-size: 1.25rem;
  font-weight: 400;
  color: #ffffffa9;
}

.social-icons {
  display: flex;
  justify-content: center; /* لضمان أن الأيقونات في المنتصف */
  gap: 40px; /* المسافة بين الأيقونات */
  margin-top: 30px; /* مسافة بين النص والأيقونات */
  opacity: 0; /* البداية بالشفافية 0 */
  animation: fadeIn 1s ease-out forwards; /* إضافة أنيمشن للظهور */
}

/* أنيمشن لتأثير الظهور */
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}


@keyframes waveAnimation {
  0% {
    clip-path: polygon(50% 10%, 100% 0%, 100% 100%, 0% 100%, 40% 50%);
  }
  25% {
    clip-path: polygon(60% 15%, 100% 0%, 100% 90%, 100% 100%, 50% 60%);
  }
  50% {
    clip-path: polygon(70% 20%, 100% 100%, 100% 80%, 0% 80%, 50% 70%);
  }
  75% {
    clip-path: polygon(60% 10%, 100% 30%, 0% 70%, 0% 70%, 40% 40%);
  }
  100% {
    clip-path: polygon(50% 10%, 100% 0%, 100% 100%, 0% 100%, 40% 50%);
  }
}

@keyframes textAnimation {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  50% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes emojiAnimation {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(15deg);
  }
  50% {
    transform: rotate(0deg);
  }
  75% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
</style>
