import { createRouter, createWebHistory } from 'vue-router';
import HomeItem from '@/views/HomeItem.vue';
import AboutItem from '@/views/AboutItem.vue';
import ProjectsItem from '@/views/ProjectsItem.vue';
import ContactItem from '@/views/ContactItem.vue';

const routes = [
  { path: '/', component: HomeItem },
  { path: '/about', component: AboutItem },
  { path: '/projects', component: ProjectsItem },
  { path: '/contact', component: ContactItem },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;