<template>
  <nav class="sidebar">
    <ul>
      <li
        v-for="(item, index) in menuItems"
        :key="index"
        :class="{ active: activeSection === item.id }"
        @click="scrollToSection(item.id)"
      >
        <div class="menu-item">
          <i :class="item.icon"></i>
        </div>
      </li>
    </ul>
  </nav>
</template>



<script>
export default {
  data() {
    return {
      menuItems: [
        { id: "home", name: "Home", icon: "bi bi-house-door" },
        { id: "about", name: "About", icon: "bi bi-person" },
        { id: "projects", name: "Projects", icon: "bi bi-briefcase" },
        { id: "skills", name: "Skills", icon: "bi bi-braces" },
        { id: "contact", name: "Contact", icon: "bi bi-envelope" },
      ],
      activeSection: "home",
      hoveredSection: null,
    };
  },
  mounted() {
    this.observeSections();
  },
  methods: {
    scrollToSection(sectionId) {
      const section = document.getElementById(sectionId);
      if (section) {
        section.scrollIntoView({ behavior: "smooth" });
        this.activeSection = sectionId;
      }
    },
    observeSections() {
      const observer = new IntersectionObserver(
        (entries) => {
          entries.forEach((entry) => {
            if (entry.isIntersecting) {
              this.activeSection = entry.target.id;
            }
          });
        },
        { threshold: 0.5 }
      );
      this.menuItems.forEach((item) => {
        const section = document.getElementById(item.id);
        if (section) observer.observe(section);
      });
    },
  },
};
</script>


<style scoped>
*{
  right: 17px;

}

.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 70px;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
  z-index: 1000;
}

.sidebar ul {
  list-style: none;
  padding: 0px 0;
  margin: 0px 0;
}

.sidebar li {
  margin: 1rem 0;
  padding: 0px 0;
  cursor: pointer;
  position: relative;
}

.sidebar li.active i {
  color: #6505ff;
  font-size: 20px;
}

.sidebar li i {
  margin: 0 0px;
  background: #6c757d11;
  border-radius: 70px;
  font-size: 20px;
  color: #6c757d;
}

.sidebar li a {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.menu-item {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #6c757d11;
  border-radius: 70px;
  padding: 10px;
  width: 40px;
  height: 40px;
  position: relative;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.menu-item:hover {
  background-color: #6505ff;
}

.menu-item i {
  font-size: 20px;
  color: #ffffff;
  transition: color 0.3s ease;
}

.menu-item:hover i { 
  color: #fff;
}

.sidebar li.active .menu-item {
  background-color: #6505ff;
}

.sidebar li.active .menu-item i {
  color: #ffffff;
}
</style>