<template>
  <div class="skills-container">
    <!-- Section Title -->
    <h2 class="skills-title">Explore My Expertise</h2>
    <p class="skills-subtitle">A showcase of the tools and technologies I excel in</p>

    <div class="skills-wrapper">
      <div 
        class="skill-item" 
        v-for="(skill, index) in skills" 
        :key="index"
        :style="getItemStyle(index)">
        <div class="skill-content">
          <img :src="require(`@/assets/icons/${skill.icon}`)" alt="icon" class="skill-icon" />
          <p class="skill-name">{{ skill.name }}</p>
        </div>
      </div>
    </div>
  </div>
</template>



<script>
import { ref, onMounted } from 'vue';
import { gsap } from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

export default {
  name: 'SkillsItem',
  setup() {
    const skills = ref([
      { name: 'Angular', icon: 'angular.svg' },
      { name: 'Java', icon: 'java.svg' },
      { name: 'Springboot', icon: 'spring.svg' },
      { name: 'MySQL', icon: 'mysql.svg' },
      { name: 'Vue.js', icon: 'vue.svg' },
      { name: 'HTML5', icon: 'html5.svg' },
      { name: 'CSS3', icon: 'css3.svg' },
      { name: 'Bootstrap', icon: 'bootstrap.svg' },
      { name: 'JavaScript', icon: 'javascript.svg' },
      { name: 'TypeScript', icon: 'typescript.svg' },
      { name: 'Jira', icon: 'jira.svg' },
      { name: 'Trello', icon: 'trello.svg' },
      { name: 'Cypress', icon: 'cypress.svg' },
      { name: 'Azure Cloud', icon: 'azure.svg' },
      { name: 'Jenkins', icon: 'jenkins.svg' },
      { name: 'Tailwind', icon: 'tailwindcss.svg' },
      { name: 'GSAP', icon: 'gsap.svg' },
    ]);

    const getItemStyle = (index) => {
      const delay = index * 0.2;
      return { animationDelay: `${delay}s` };
    };

    const animateHorizontalScroll = () => {
      const wrapper = document.querySelector('.skills-wrapper');

      // جعل الحاوية نفسها تعمل كـ scroll animation
      gsap.to(wrapper, {
        x: () => -(wrapper.scrollWidth - wrapper.clientWidth),
        ease: 'linear',
        scrollTrigger: {
          trigger: '.skills-container',
          start: 'center center',
          end: '+=2000px', // طول التمرير الأفقي
          scrub: true,
          pin: true, // تثبيت الحاوية أثناء التمرير
        },
      });

      // تأثير ظهور العناصر
      gsap.fromTo(
        '.skill-item',
        { opacity: 0, scale: 0.8 }, // الحالة الابتدائية
        {
          opacity: 1,
          scale: 1,
          stagger: 0.1,
          scrollTrigger: {
            trigger: '.skills-wrapper',
            start: 'left center',
            end: 'right center',
            scrub: true,
          },
        }
      );
    };

    onMounted(() => {
      animateHorizontalScroll();
    });

    return {
      skills,
      getItemStyle,
    };
  },
};
</script>


  <style scoped>
.skills-container {
  overflow-x: auto hidden;
  background-color: transparent;
  padding: 20px;
  margin-top: 100px;
  margin-bottom: 100px;
}

.skills-wrapper {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px; /* مسافة بين العناصر */
  width: 100%;
  scroll-behavior: smooth; /* تمرير ناعم */
}

.skill-item {
  flex: 0 0 250px; /* عرض ثابت */
  background-color: #fff;
  border: 2px solid #3700b89f;
  border-radius: 40px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  margin-right: 20px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  transition: transform 0.3s ease;
  opacity: 0; /* الوضع الابتدائي */
}

.skill-item:hover {
  transform: scale(1.2);
}

.skill-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.skill-icon {
  width: 70px;
  height: 70px;
}

.skill-name {
  font-family: 'Marcellus', serif;
  color: #ae0afa;
  font-size: 24px;
  text-transform: uppercase;
}
</style>

