<template>
  <div class="carousel-container">
    <button class="carousel-prev" @click="goPrev">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
        <path d="M15 18L9 12L15 6" />
      </svg>
    </button>
    <button class="carousel-next" @click="goNext">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
        <path d="M9 18L15 12L9 6" />
      </svg>
    </button>

    <Flicking
      ref="flicking"
      class="carousel"
      :circular="true"
      :gap="20"
      :duration="500"
      :panelsPerView="2">
      <div
  v-for="(slide, index) in slides"
  :key="index"
  class="carousel-item"
  :style="{ backgroundColor: slide.bgColor }"
>
  <img :src="slide.imageUrl" alt="Project Image" class="project-image" />
  <p class="project-description">{{ slide.description }}</p>
  <div class="technologies">
    <h4>Technologies Used</h4>
    <ul class="tech-list">
      <li v-for="(tech, techIndex) in slide.technologies" :key="techIndex">
        <img :src="tech.icon" :alt="tech.name" class="tech-icon" />
      </li>
    </ul>
  </div>
  <a :href="slide.projectLink" target="_blank" class="project-link">View Project</a>
</div>

    </Flicking>
  </div>
</template>

<script>
import { ref } from "vue";
import Flicking from "@egjs/vue3-flicking";
import "@egjs/vue3-flicking/dist/flicking.css";

export default {
  name: "ProjectsItem",
  components: {
    Flicking,
  },
  props: {
    slides: {
      type: Array,
      default: () => [
        {
          text: "SDK Akademie",
          description: "SDK Akademie is an Angular 18-based platform for learning German, offering secure online and in-person courses",
          bgColor: "#f3f4f6",
          imageUrl: require('@/assets/images/project1.jpg'),
          projectLink: "https://sdk-akademie.vercel.app/ ",
          technologies: [
          { name: "Angular", icon: require('@/assets/icons/angular.svg') },
          { name: "Firebase", icon: require('@/assets/icons/cypress.svg') },
          { name: "Tailwind CSS", icon: require('@/assets/icons/tailwindcss.svg') },
        ],
        },
        {
          text: "Second Project",
          description: "An online database providing detailed information about shop products and their online shopping cart data, integrated with the E-Commerce API.",
          bgColor: "#e5e7eb",
          imageUrl: require('@/assets/images/project2.jpg'),
          projectLink: "https://shop-smart-kappa.vercel.app/home",
          technologies: [
        { name: "Angular", icon: require('@/assets/icons/angular.svg') },
        { name: "Bootstrap", icon: require('@/assets/icons/bootstrap.svg') },
        { name: "Firebase", icon: require('@/assets/icons/firebase.svg') },
        ],
        },
        {
          text: "Third Project",
          description: "HM Restaurant for reservation -incomplete-.",
          bgColor: "#d1d5db",
          imageUrl: require('@/assets/images/project3.jpg'),
          projectLink: "#",
          technologies: [
          { name: "Angular", icon: require('@/assets/icons/angular.svg') },
          { name: "Bootstrap", icon: require('@/assets/icons/bootstrap.svg') },
    ],
        },
        ],
    },
  },
  setup() {
    const flicking = ref(null);

    const goPrev = () => {
      const currentIndex = flicking.value.index; 
      if (currentIndex > 0) {
        flicking.value.prev();
      }
    };

    const goNext = () => {
      const currentIndex = flicking.value.index; 
      if (currentIndex < flicking.value.panels.length - 1) {
        flicking.value.next();
      }
    };

    return {
      flicking,
      goPrev,
      goNext,
    };
  },
};
</script>

<style scoped>

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Marcellus', serif;
}

.body { 
  background-color: #38114754;

}

.carousel-container {
  height: 800px;
  width: 1000px;
  margin: 0 0 0 70px;
  position: relative;
  background-color: #3700b89f;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  border-radius: 90px 50px 50px 90px;
}

.carousel-item {
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 750px;
  width: 550px;
  border-radius: 10px;
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  padding: 10px;
  transition: transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  background-color: #512da8;
  border-radius: 50px 50px 50px 50px;

}

.carousel-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.5);
}

.carousel-prev,
.carousel-next {
  position: absolute;
  top: 50%;
  transform: translateY(-70%);
  width: 70px;
  height: 70px;
  border: none;
  border-radius: 50%;
  background-color: #e7e6eb;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 10;
  transition: background-color 0.3s ease;
}

.carousel-prev {
  left: -75px;
}

.carousel-next {
  right: -75px;
}

.carousel-prev:hover,
.carousel-next:hover {
  background-color: #311b92b9;
}

.carousel-prev svg,
.carousel-next svg {
  width: 24px;
  height: 24px;
}

.project-image {
  width: 100%;
  height: 450px;
  border-radius: 50px 40px 0px 0px;
  object-fit: cover;
  margin-bottom: 5px;
}

.project-title {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 10px;
}

.project-description {
  font-size: 16px;
  color: #19001f;
  margin-bottom: 5px;
  background-color: transparent;
}

.project-link {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  background-color: #311b92;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.project-link:hover {
  background-color: #4527a0;
}

.technologies {
  margin: 10px;
  text-align: center;
  gap: -10px;
  color: rgb(53, 1, 54);
  font-size: 20px;
}

.tech-list {
  margin: 10px;
  display: flex;
  padding: 0;
  gap: 10px;
  background-color: transparent;
}

.tech-list li {
  display: flex;
  align-items: center;
  gap: -10px; /* تقليل المسافة بين الأيقونة والنص */
  background-color: transparent;
}

.tech-icon {
  width: 46px; /* حجم الأيقونة */
  height: 46px;
  object-fit: contain; /* الحفاظ على أبعاد الأيقونة */
  filter: none; /* إزالة أي تأثيرات لونية */
  color: inherit; /* استخدام لون الأيقونة الطبيعي */
}

.tech-list li span {
  font-size: 12px; /* تصغير حجم النص */
  color: #ffffff; /* لون محايد للنص */
}


</style>
